import React from "react";
import { Link } from "react-router-dom";
import { logout } from "../actions/userActions";
import { useDispatch, useSelector } from "react-redux";

import logo from "../logo.png";

const Header = () => {
  const dispatch = useDispatch();

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const logoutHandler = () => {
    dispatch(logout());
  };
  return (
    <div>
      <nav
        className="  z-40 w-full fixed top-0 shadow bg-white "
        role="navigation"
      >
        <div className="container max-w-7xl  mx-auto   flex flex-wrap items-center md:flex-no-wrap">
          <div className=" md:mr-8">
            <Link to="/dashboard">
              <img alt="" src={logo} className="h-12" />
            </Link>
          </div>
          <div className="ml-auto md:hidden">
            <button
              className="flex items-center px-3 py-2 border rounded"
              type="button"
            >
              <svg
                className="h-3 w-3"
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <title>Menu</title>
                <path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" />
              </svg>
            </button>
          </div>
          <div className="w-full md:w-auto  md:flex-grow md:flex md:items-center hidden">
            <ul className="flex flex-col mt-4  -mx-4 pt-4 border-t md:flex-row md:items-center  md:mx-auto md:mt-0 md:pt-0 md:border-0">
              <li>
                <Link
                  to="/dashboard"
                  className="block px-4 py-1 font-normal   md:p-2 lg:px-5  hover:text-primary-500  text-sm"
                >
                  Dashboard
                </Link>
              </li>
              <li>
                <Link
                  to="/homeSlider"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm "
                >
                  Home Slider
                </Link>
              </li>
              <li>
                <Link
                  to="/news"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm "
                >
                  In News
                </Link>
              </li>
              <li>
                <Link
                  to="/partner"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm "
                >
                  Partners
                </Link>
              </li>
              <li>
                <Link
                  to="/collaborator"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm "
                >
                  Collaborators
                </Link>
              </li>
              <li>
                <Link
                  to="/team"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm "
                >
                  Team
                </Link>
              </li>
              <li>
                <Link
                  to="/superstarDonor"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm "
                >
                  Superstar Donors
                </Link>
              </li>
              <li>
                <Link
                  to="/Blog"
                  className="block px-4 py-1 font-normal  md:p-2 lg:px-5 hover:text-primary-500 text-sm"
                >
                  Blog
                </Link>
              </li>
            </ul>
            <div className="block px-4 py-1 font-normal md:p-2 lg:pl-10 ">
              <button
                onClick={logoutHandler}
                className="flex px-5 py-2 text-sm font-normal leading-6 text-center text-primary-50  transition bg-primary-500 rounded shadow ripple hover:shadow-lg hover:bg-primary-600 focus:outline-none"
              >
                Logout
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="h-6 text-primary-50  w-6 ml-2"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Header;
