import { UserCircleIcon } from "@heroicons/react/outline";
import CountUp from "react-countup";
import VisibilitySensor from "react-visibility-sensor";
import React, { useEffect } from "react";

import { useSelector } from "react-redux";

// import { Link } from 'react-router-dom'

const Homescreen = ({ history }) => {
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;
  useEffect(() => {
    if (!userInfo) {
      history.push("/mbdd_admin");
    }
  }, [history, userInfo]);
  return (
    <div className="bg-primary-50  py-20">
      <div>
        <div class="relative w-full mx-auto mb-10 bg-white z-20 overflow-hidden transition-shadow duration-300   rounded-lg  lg:max-w-6xl group ">
          <div class="relative flex mx-auto flex-col items-center justify-around  h-full py-5 duration-300  rounded-sm transition-color sm:items-stretch sm:flex-row">
            <div class="w-1/3 py-8 text-center">
              <div className="h-10 w-10 mx-auto mb-5 flex items-center jusity-center">
                <img className="mx-auto" src="blood-donation.svg" alt="" />
              </div>
              <h6 class="text-4xl font-bold text-primary-500 sm:text-3xl ">
                <CountUp start={0} end={104978} redraw={true}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start} delayedCall>
                      <span class="" ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                +{/* <span class="counter"></span>+ */}
              </h6>
              <p class="text-center md:text-base text-primary-500">
                Blood Units Collected
              </p>
            </div>

            <div class="w-1/3 py-8 text-center">
              <div className="h-10 w-10 mx-auto mb-5 flex items-center jusity-center">
                <img className="mx-auto" src="blood-donation.svg" alt="" />
              </div>
              <h6 class="text-4xl font-bold text-primary-500 sm:text-3xl ">
                <CountUp start={0} end={314561} redraw={true}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start} delayedCall>
                      <span class="" ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                +
              </h6>
              <p class="text-center md:text-base text-primary-500">
                Lives Saved
              </p>
            </div>

            <div class="w-1/3 py-8 text-center">
              <div className="h-10 w-10  rounded-full mx-auto mb-5 flex items-center jusity-center">
                <img className="mx-auto" src="blood-donation.svg" alt="" />
              </div>
              <h6 class="text-4xl font-bold text-primary-500 sm:text-3xl ">
                <CountUp start={0} end={1041} redraw={true}>
                  {({ countUpRef, start }) => (
                    <VisibilitySensor onChange={start} delayedCall>
                      <span class="" ref={countUpRef} />
                    </VisibilitySensor>
                  )}
                </CountUp>
                +
              </h6>
              <p class="text-center md:text-base text-primary-500">
                Camps Organized across India
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="max-w-6xl mx-auto  flex items-center justify-center  bg-white h-96   shadow-sm">
        <div className="mx-auto ">
          <UserCircleIcon
            className="h-24 w-24 mx-auto text-primary-400  "
            aria-hidden="true"
          />
          <h1 className="text-5xl items-center font-semibold text-center text-primary-500">
            {" "}
            Welcome Admin
          </h1>
        </div>
      </div>
    </div>
  );
};

export default Homescreen;
